@media only screen and (max-width: 479px) {
  html {
    font-size: 75%;
  }
}
.contact-form {
   margin: 8em auto;
}

.contact-form h2 {
   margin-bottom: 1em;
   text-align: center;
   text-transform: uppercase;
}

.contact-form Form {
   width: 33%;
   margin: 0 auto;
}

.contact-form input {
   width: 100%;
   height: 30px;
   margin-bottom: .25em;
   font-size: 1.25rem;

}
.contact-form span {
   display: block;
   width: 100%;
   height: 15px;
   margin-bottom: .5em;
   color: rgb(245, 41, 41);
   font-size: .75rem;
   font-weight: 800;
}

.contact-form button {
   display: block;
   height: 50px;
   width: 100%;
   margin-top: .75em;
   font-size: 1.5rem;
   letter-spacing: 1px;
}

/*mobile landscape layout (480px to 1024px) */
@media only screen and (max-width: 1024px) {
   /*...mobile landscape styles */
   .contact-form Form {
      width: 66.6%;
   }
}

/*mobile portrait layout (479px or less) */
@media only screen and (max-width: 479px) {
   /*...mobile portrait styles */
   .contact-form Form {
      width: 83.3%;
   }
}
.center {
  text-align: center;
}

.center h1 {
  margin: 1em auto;
}
